.body_contedor {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #ff7f50;
    background-image: linear-gradient(180deg, #f65750 0%, white 100%);
}

.container_services {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 1000px;
    flex-wrap: wrap;
    z-index: 1;
}

.container_services .card {
    position: relative;
    width: 280px;
    height: 400px;
    margin: 30px;
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);
}

.container_services .card .content_service {
    padding: 20px;
    text-align: center;
    transform: translateY(10px);
    transition: 0.5s;
    font-family: "Poppins" sans-serif;
    color: #1c1c1c;
}

.container_services .card:hover .content_service {
    transform: translateY(0px);
    transform-style: preserve-3d;
    transform: perspective(1000px);
    transform: translateZ(20px);
}

.container_services .card .content_service h2 {
    position: absolute;
    /* right: 30px; */
    font-size: 3em;
    color: rgba(255, 255, 255, 0.20);
    pointer-events: none;
    text-transform: uppercase;
}

.container_services .card .content_service h3 {
    font-size: 1.8em;
    color: #1c1c1c;
    z-index: 1;
}

.container_services .card .content_service p {
    font-size: 1em;
    color: #1c1c1c;
    font-weight: 300px;
}

.container_services .card .content_service a {
    position: relative;
    display: inline-block;
    padding: 8px 20px;
    margin-top: 15px;
    background: #1c1c1c;
    color: white;
    border-radius: 20px;
    text-decoration: none;
    font-weight: 500;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.container_services .card .content_service a:hover {
    background: white;
    color: #1c1c1c;
}